/**
 * Customsations to the columns layout
*/

.v24-block-columns__icons {
	img {
		height: 50px;
	}
}

// This is the block that is used at the bottom of most page.
// Has a grey border and three images inside that are dinky.
.v24-block-columns__little-images--with-border {
	border: 2px solid $v24-light-grey;
	padding: rem($medium-spacing) 0;
	justify-content: center;
	max-width: 700px;
	margin: auto;

	.wp-block-column {
		max-width: 200px;
		flex-basis: 0;
	}

	.wp-block-image {
		margin-bottom: 0;
	}
}
