/**
 * Custom clasess that can be applied to Gutenberg blocks.
*/

/* 1. GENERAL CLASSES     */
/* 2. COLOUR CLASSES      */


/* 1. GENERAL CLASSES     */
.container-width-on-desktop {

	@include media-breakpoint-up(lg) {
		width: 100%;
		margin: auto;
		position: static;	
	}
}



/* 2. COLOUR CLASSES      */
.block-bg__light-grey {
	background-color: $v24-light-grey;
	height: 130px;
	width: 130px;
	border-radius: 100px;
	margin: 0 auto $small-spacing;

	figure {
		display: flex !important;
		justify-content: center;
		align-items: center;
		height: 100%;
	}

	img { 
		height: 80px;
		width: 80px;
	}
}

.block-border__light_grey {
	border: 2px solid $v24-light-grey;
}