/*
* Relates to template-parts/partials/airline-teaser.php
*/

.airline__teasers {
	margin-bottom: rem($large-spacing*2);
}

.airline__teaser {
	padding: rem($medium-spacing);
	margin: $medium-spacing 0;
	display: flex;

	&:nth-of-type(odd) {
		background-color: $v24-light-grey;
	}
}

.airline__teaser--image {
	width: 140px;
	background-color: $v24-white;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 5px;
}

.airline__teaser--details {
	margin-left: rem( $large-spacing );
}

.airline__teaser--doclink {
	font-weight: $bold-font-weight;
}
