/** 
 * Styling relating to the medif downloads page.
 * This page is the archive page for the CPT 'airline' as well
*/

.airline__filters {
	margin: rem($large-spacing*2) 0;

	@include media-breakpoint-up(md) {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
}

.reusable-blocks > div {
	max-width: none !important;
	margin-left: -50vw !important;
} 

.airline__search--no-results {
	padding-bottom: rem($medium-spacing);
	margin-bottom: rem($medium-spacing);
}