/**
 * Styling / layout for the header
*/

.hfg_header.site-header {
	box-shadow: none;
}
.header-main-inner {
	padding: rem($small-spacing) 0;

	@media (min-width: 960px) {
		padding: rem($large-spacing) 0;
	}
}
.site-logo img {
	width: 100%;
	max-width: none;
}

/* Mobile menu icon */
.builder-item--nav-icon .navbar-toggle {
	padding-right: 0;
}
.icon-bar {
	width: 25px;
	height: 3px;
}
.icon-bar:nth-child(2) {
	margin: 5px 0;
}
.is-menu-sidebar .navbar-toggle .icon-bar:nth-child(1) {
	top: 11px;
}

/* Accent box - red box with left hand triangle */
.header__accent-box {
	display: inline-block;
	height: 0;
	border-style: solid;
	border-width: 0 0px 81px 50px;
	border-color: transparent transparent $v24-red;
}
.header__accent-box--inner {
	display: inline-block;
	background-color: $v24-red;
	color: white;
	font-family: $title-font;
	padding: rem($medium-spacing) rem($large-spacing) rem($medium-spacing) 0;
}
