/*
* Appears on the medif download page and lists all the categories (which are letters).
*/

.alphabet-menu {
	padding-bottom: rem($large-spacing);
	// margin-top: rem( -$small-spacing );
	margin-right: rem($large-spacing);
	margin-bottom: rem(-$small-spacing);
	display: inline-block;

	@include media-breakpoint-up(md) {
		padding-bottom: 0;
	}
}

.alphabet-menu__char {
	display: inline-block;
	padding: 0 rem( $small-spacing ) rem( $small-spacing ) 0;

	@include media-breakpoint-up(lg) {
		padding-bottom: 0;
	}

	a {
		border-bottom: 1px solid $default-font-colour;
		line-height: 1.2;
		color:$default-font-colour;

		&:focus, &:hover {
			border-bottom: 2px solid currentColor;
			text-decoration: none;
			color: $v24-red;
		}
	}
}