/**
 * Styling and layout for the footer.
*/

.footer-bottom-inner {
	padding: rem( $large-spacing ) 0;
}

.site-footer {
	.widget_media_image img {
		max-width: 300px !important;
	}
}

.footer--row {
	
	@include media-breakpoint-down(md) {

		.hfg-item-left {
			padding-left: rem( $large-spacing*2 );
		}

		.hfg-item-right {
			padding-right: rem( $large-spacing*2 );
		}
	}
	@include media-breakpoint-down(xs) {

		.hfg-item-left {
			padding-left: rem( $large-spacing );
		}

		.hfg-item-right {
			padding-right: rem( $large-spacing );
		}
	}

}


